import React, { useState } from "react";

function App()
{
    var d = new Date().toLocaleTimeString();
let[val,updatedVal]=useState(d);
function UpdatedTime(){
    updatedVal(new Date().toLocaleTimeString());
}
setInterval(UpdatedTime,1000);
return(<>
<div className="cardinfo">
<h1 >{val}</h1>
<h2 className="sub_heading">Digital Clock</h2>
</div>
</>
);
}
export default App;